import React, { PureComponent,Fragment} from 'react'
import IcAvatar from '@eui/ic-avatar'
import Markdown from 'ic-markdown'
import unknow from './unknow.md'
import female from './female.md'
import male from './male.md'
import onClick from './click.md'
export default class extends PureComponent {

    render() {
        return (
            <Fragment>
                <section>默认头像</section>
                <section>
                   <IcAvatar size='tiny'/>
                   <IcAvatar size='small'/>
                   <IcAvatar size='medium'/>
                   <IcAvatar size='large'/>
                   <IcAvatar size='giant'/>
                </section>
                <section>
                    <Markdown>{unknow}</Markdown>
                </section>
                <section>男性头像</section>
                <section>
                   <IcAvatar size='tiny' gender='male'/>
                   <IcAvatar size='small' gender='male'/>
                   <IcAvatar size='medium' gender='male'/>
                   <IcAvatar size='large' gender='male'/>
                   <IcAvatar size='giant' gender='male'/>
                </section>
                <section>
                    <Markdown>{male}</Markdown>
                </section>
                <section>女性头像</section>
                <section>
                   <IcAvatar size='tiny' gender='female'/>
                   <IcAvatar size='small' gender='female'/>
                   <IcAvatar size='medium' gender='female'/>
                   <IcAvatar size='large' gender='female'/>
                   <IcAvatar size='giant' gender='female'/>
                </section>
                <section>
                    <Markdown>{female}</Markdown>
                </section>
                <section>可点击头像</section>
                <section>
                   <IcAvatar size='tiny' gender='unknow' onClick={()=>console.log('clicked')}/>
                   <IcAvatar size='small' gender='male' onClick={()=>console.log('clicked')}/>
                   <IcAvatar size='medium' gender='female' onClick={()=>console.log('clicked')}/>
                   <IcAvatar size='large' gender='male' onClick={()=>console.log('clicked')}/>
                   <IcAvatar size='giant' gender='female' onClick={()=>console.log('clicked')}/>
                </section>
                <section>
                    <Markdown>{onClick}</Markdown>
                </section>
            </Fragment>
        );
    }
}



